<template>
  <div>
    <Header></Header>
    <div class="base-content abc-flex-x-between">
      <Left></Left>
      <div class="add-project">
        <div class="title">
          
        </div>

        <div class="content">
          <div class="abc-flex-x-start">
            <div class="content-title"></div>
            <div>ABC</div>
          </div>

          <!---->
          <div class="abc-flex-x-start">
            <div class="content-title"></div>
            <div class="person-info-par">
              <div class="person-info">
                <select v-for="(item, index) in personList" v-model="personList[index]">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                  <option value="5"></option>
                  <option value="6"></option>
                  <option value="7"></option>
                </select>

              </div>

              <div @click="addPerson" class="add-person abc-img"><img src="../../../assets/img/payroll/addProject/add_icon.png"></div>
              <div v-if="personList.length > 1" @click="removePerson"
                   class="remove-person abc-img"><img src="../../../assets/img/payroll/addProject/remove_icon.png"></div>

            </div>
          </div>

          <!---->
          <div class="time-all">
            <div class="data-detail">
              <div class="list-header abc-flex-x-start">
                <div style="width: 150px;"></div>
                <div></div>
              </div>

              <div class="list-body">

                <div v-for="(item, index) in personList_new" class="body-item abc-flex-x-start">
                  <div style="width: 150px;">{{ item.name || '-' }}</div>
                  <div><input v-model="item.hours" @input="changeHours(item, index)"
                              type="text" onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'></div>
                </div>

              </div>

            </div>
          </div>

          <div class="abc-flex-x-start">
            <div class="content-title"></div>
            <div>180</div>
          </div>

          <div class="abc-flex-x-start">
            <div class="content-title"></div>
            <div>10000</div>
          </div>

          <div class="start-time abc-flex-x-start">
            <div class="content-title"></div>
            <div class="abc-flex-x-start">
              <div class="abc-img"><img src="../../../assets/img/payroll/addProject/date.png"></div>
              <div class="start-time-input"><input type="text" /></div>
            </div>
          </div>

          <div class="end-time abc-flex-x-start">
            <div class="content-title"></div>
            <div class="abc-flex-x-start">
              <div class="abc-img"><img src="../../../assets/img/payroll/addProject/date.png"></div>
              <div class="end-time-input"><input type="text" /></div>
            </div>
          </div>

          <div class="status abc-flex-x-start">
            <div class="content-title"></div>
            <div class="complete">
              <select>
                <option></option>
                <option></option>
              </select>
            </div>
          </div>

        </div>

        <div class="btn-group abc-flex-x-start">
          <div class="abc-btn"></div>
          <a class="abc-btn" href="./payroll.html"></a>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import $ from 'n-zepto'
//import { getList } from '@/api/test'
import { personMapList } from './computed/index'

import Header from '../../common/header.vue'
import Left from '../../common/left.vue'

export default {
  name: 'addProject',
  data () {
    return {
      personList_result: [], // ()







      personList: ['0'], // id

    }
  },
  computed: {
    // 
    personList_new () {
      let arr = []
      this.personList.map((item, index) => {

        // hours***
        const hours = this.personList_result.length && this.personList_result[index] ? this.personList_result[index].hours : ''

        const obj = {
          id: item,
          name: personMapList(item),
          hours: hours
        }

        arr.push(obj)
      })

      return arr
    }



  },
  created () {

  },
  watch: {

    personList (val, valOld) {
      console.log(val)


    },
    personList_new (val, valOld) {
      console.log(val)


    }



  },
  async mounted () {

  },
  methods: {
    // 
    addPerson () {
      // 
      const newList = this.personList.slice()
      newList.push('0')
      this.personList = newList

      // 
      const newList_result = this.personList_result.slice()
      newList_result.push({id: '', name: '', hours: ''}) // .
      this.personList_result = newList_result
    },

    // 
    removePerson () {
      // 
      const newList = this.personList.slice()
      newList.pop()
      this.personList = newList

      // 
      const newList_result = this.personList_result.slice()
      newList_result.pop()
      this.personList_result = newList_result
    },

    // 
    changeHours (item, index) {
      console.log(item)
      console.log(index)

      const personListNew = this.personList_new.slice()
      this.personList_result = personListNew



    }

  },
  components: {
    Header,
    Left
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

  .base-content {
    margin: 0 auto;
    width: 1200px;
    min-height: 800px;
    align-items: flex-start;
  }
  .add-project {
    width: 980px;
    min-height: 1000px;

    .title {
      height: 50px;
      width: 980px;
      border-bottom: 1px solid #979797;
      line-height: 50px;
      font-size: 20px;
      text-align: left;
    }

    .content {
      & > div {
        margin-top: 12px;
        text-align: left;

        .content-title {
          width: 150px;
        }

        .person-info-par {
          width: 800px;
          overflow: hidden;

          .person-info {
            float: left;
            overflow: hidden;
            select {
              float: left;
              margin-right: 10px;
              width: 100px;
              height: 30px;
            }
          }

          .add-person {
            float: left;
            cursor: pointer;
            width: 20px;
            height: 20px;
            margin-top: 5px;
            margin-right: 5px;
          }
          .remove-person {
            float: left;
            cursor: pointer;
            margin-top: 6px;
            width: 18px;
            height: 18px;
          }

        }

        .data-detail {
          margin-left: 150px;
          .list-header {
            background: #E1E1E1;
            width: 400px;
            height: 40px;
            padding: 10px 0 10px 10px;
            & > div {
              width: 170px;
              margin-right: 20px;
            }
            .operate {
              margin-right: 0;
            }
          }

          .list-body {
            width: 400px;
            .body-item {
              width: 400px;
              height: 40px;
              padding: 10px 0 10px 10px;
              border-bottom: 1px solid #CACACA;
              & > div {
                width: 170px;
                margin-right: 20px;

                input {
                  width: 80px;
                  height: 30px;
                  background: #f0f0f0;
                  color: #000;
                  padding: 0 5px;
                }
              }
              .operate {
                margin-right: 0;
                & > div {
                  cursor: pointer;
                }
              }
            }
          }
        }

        .start-time-input input, .end-time-input input {
          width: 120px;
          height: 30px;
          border: 1px solid #ccc;
          border-radius: 5px;
          margin-left: 10px;
          padding: 0 5px;
        }

        .complete {
          select {
            width: 100px;
            height: 30px;
          }
        }

      }

    }

    .btn-group {
      margin-top: 100px;
      .abc-btn {
        margin-right: 20px;
        color: #fff;
      }
    }




  }

</style>



